<template>
  <div class="gallery-container">
    <!--   -->
    <nav class="navbar">
      <div class="nav-left">
        <router-link to="/hoonlog/" class="logo">HoonLog</router-link>
        <span class="divider">/</span>
        <router-link to="/hoonlog/" class="nav-text link">? </router-link>
        <span class="divider">/</span>
        <span class="nav-text">Stories</span>
      </div>
    </nav>

    <!--   -->
    <section class="photo-list">
      <div
        v-for="(image, index) in images"
        :key="index"
        class="photo-item"
        @click="openModal(index)"
      >
        <img :src="image" alt="gallery photo" loading="lazy" />
      </div>
    </section>

    <!--   -->
    <transition name="fade">
      <div v-if="showModal" class="modal-overlay" @click.self="closeModal">
        <div
          class="carousel-track"
          :style="{ transform: `translateX(-${currentIndex * 100}%)` }"
          @touchstart="handleTouchStart"
          @touchmove="handleTouchMove"
          @touchend="handleTouchEnd"
        >
          <div
            v-for="(image, index) in images"
            :key="index"
            class="carousel-slide"
          >
            <img :src="image" class="modal-image" />
          </div>
        </div>

        <button class="nav-btn prev" @click.stop="prevImage"></button>
        <button class="nav-btn next" @click.stop="nextImage"></button>
        <button class="close-btn" @click="closeModal"></button>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {return {
  showModal: false,
  currentIndex: 0,
  startX: 0,
  deltaX: 0,
  maxImages: 23,
  images: Array.from({ length: 23 }, (_, index) => 
    require(`@/assets/2022i/2022i_${index + 1}.jpg`)
  ),
};
  },
  methods: {
    openModal(index) {
      this.currentIndex = index;
      this.showModal = true;
      document.body.style.overflow = "hidden";
    },
    closeModal() {
      this.showModal = false;
      document.body.style.overflow = "";
    },
    nextImage() {
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
    },
    prevImage() {
      this.currentIndex =
        (this.currentIndex - 1 + this.images.length) % this.images.length;
    },
    handleTouchStart(e) {
      this.startX = e.touches[0].clientX;
    },
    handleTouchMove(e) {
      this.deltaX = e.touches[0].clientX - this.startX;
    },
    handleTouchEnd() {
      if (this.deltaX > 50) {
        this.prevImage();
      } else if (this.deltaX < -50) {
        this.nextImage();
      }
      this.deltaX = 0;
    },
  },
};
</script>

<style scoped>
/*   */
.gallery-container {
  background-color: #fff;
  color: #222;
  font-family: "Helvetica Neue", Arial, sans-serif;
  min-height: 100vh;
  overflow-x: hidden;
}

/*   */
.navbar {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 80px;
  font-size: 14px;
  letter-spacing: 1px;
}
.nav-left {
  display: flex;
  align-items: center;
  gap: 8px;
}
.logo {
  font-weight: bold;
  color: black;
  text-decoration: none;
  transition: opacity 0.3s;
}
.logo:hover {
  opacity: 0.6;
}
.divider {
  color: #aaa;
}
.nav-text {
  color: #777;
}
.link {
  text-decoration: none;
  color: #555;
  transition: opacity 0.3s;
}
.link:hover {
  opacity: 0.6;
}

/*   */
.photo-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
  padding: 40px 0 80px;
}
.photo-item {
  width: 60%;
  cursor: pointer;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.photo-item:hover {
  transform: scale(1.02);
  opacity: 0.9;
}
.photo-item img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

/*  */
@media (max-width: 1024px) {
  .photo-item {
    width: 80%;
  }
  .navbar {
    padding: 20px 40px;
  }
}
@media (max-width: 600px) {
  .photo-item {
    width: 90%;
  }
  .navbar {
    padding: 20px 20px;
    font-size: 12px;
  }
}

/*  */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  overflow: hidden;
}

/*   */
.carousel-track {
  display: flex;
  transition: transform 0.5s ease;
  width: 100%;
  height: 100%;
}
.carousel-slide {
  flex: 0 0 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-image {
  max-width: 90%;
  max-height: 90%;
  object-fit: contain;
}

/*   */
.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 50px;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
  padding: 20px;
  transition: opacity 0.3s ease;
  user-select: none;
}
.nav-btn:hover {
  opacity: 0.7;
}
.prev {
  left: 40px;
}
.next {
  right: 40px;
}

/*   */
.close-btn {
  position: absolute;
  top: 40px;
  right: 50px;
  font-size: 40px;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.close-btn:hover {
  opacity: 0.7;
}

/*    */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.4s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
